import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover";
import { IProfileParam } from "@/gateway/step";
import useDeviceContext from "@/hooks/useDeviceContext";
import { SliderField } from "@buildo/bento-design-system";
import { produce } from "immer";
import { useTranslation } from "next-export-i18n";
import { useEffect, useRef, useState } from "react";
import { IoIosCloseCircle } from "react-icons/io";

export function TimeRune({
  // value,
  //   onChange,
  // progress
  index,
  subIndex = -1,
}: {
  // value: number;
  //       onChange: Dispatch<SetStateAction<number>>;
  //      progress:  number;
  index: number;
  subIndex: number;
}) {
  const [isOpen, setIsOpen] = useState(false);
  const [current, setCurrent] = useState<number>(-1);
  const { formulas, setFormulas, selectedFormulaId } = useDeviceContext();
  const { formulaFeedbacks } = useDeviceContext();
  const step = formulas[selectedFormulaId].steps[index];

  const [value, setValue] = useState<number>(
    subIndex == -1
      ? step.target.value
      : (step.action as IProfileParam).segments[subIndex]?.duration || 0,
  );

  /*
    Progress doesn't always show up, it will disappear after 5 seconds
    So after 5 seconds, it must be clear to null
    So the number has its own internal state
  */

  let timeout = useRef(null);

  // useEffect(() => {
  //   setCurrent(progress);

  //   clearTimeout(timeout.current);
  //   timeout.current = setTimeout(() => {
  //     setCurrent(null);
  //   }, 5000);
  // }, [progress]);

  useEffect(() => {
    setValue(
      subIndex == -1
        ? step.target.value
        : (step.action as IProfileParam).segments[subIndex].duration,
    );
  }, [formulas[selectedFormulaId].steps[index].target.value]);

  useEffect(() => {
    const updated = formulaFeedbacks[selectedFormulaId]?.[index]?.target?.value;
    // if you have subIndex, there nis no need to update progress, handled by the line chart already, more visual
    if (subIndex == -1) {
      setCurrent(updated || -1);
    }
  }, [formulaFeedbacks, index, selectedFormulaId]);
  const { t } = useTranslation();

  return (
    <Popover
      onOpenChange={(e) => {
        setIsOpen(e != false);
      }}
      open={isOpen}
    >
      <PopoverTrigger>
        <div
          className="flex h-8 w-fit items-center justify-between rounded-lg p-2"
          onClick={() => {
            setIsOpen(true);
          }}
        >
          {current != -1 && (
            <h1
              className=" text-xl font-bold text-green-800"
              onClick={() => setIsOpen(true)}
            >
              {`${(current / 1000).toFixed(1)}/`}
            </h1>
          )}

          <h1 className=" text-xl font-bold text-blue-800">
            {value} {value <= 1 ? t("second") : t("seconds")}
          </h1>
        </div>
      </PopoverTrigger>
      <PopoverContent className="w-80 space-y-8 rounded-xl  border">
        <IoIosCloseCircle
          className="absolute right-2 top-2 text-gray-500"
          size={20}
          onClick={() => {
            setIsOpen(false);
          }}
        />

        <SliderField
          value={value}
          onChange={(e: number) => {
            setValue(e);
            setFormulas(
              produce(formulas, (draft) => {
                // draft[selectedFormulaId].steps[index].target.value = e;
                if (subIndex == -1) {
                  draft[selectedFormulaId].steps[index].target.value = e;
                } else {
                  const action = step.action as IProfileParam;
                  action.segments[subIndex].duration = e;
                }
              }),
            );
          }}
          label={t("Time")}
          type="single"
          name="value"
          minValue={0}
          maxValue={60}
          step={1}
        />
      </PopoverContent>
    </Popover>
  );
}
