import { Banner, Modal, SliderField, Stack } from "@buildo/bento-design-system";
import { Dispatch, SetStateAction, useState } from "react";
import { useEffectOnce } from "usehooks-ts";

export const PumpRune = ({
  value,
  onChange,
}: {
  value: number;
  onChange: Dispatch<SetStateAction<number>>;
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [power, setPower] = useState<number>(value || 100);

  useEffectOnce(() => {
    onChange(power)
  })

  return (
    <>
      <div
        className="flex h-8 w-fit items-center justify-between rounded-lg p-2"
        onClick={() => {
          setIsOpen(true);
        }}
      >
        <h1 className="text-xl font-bold text-blue-800">{value || 100} %</h1>
      </div>

      {isOpen && (
        <Modal
          size="small"
          title="Set pump power"
          onClose={() => setIsOpen(false)}
          primaryAction={{
            label: "OK",
            onPress: () => {
              setIsOpen(false);
              console.log(" update power", power);
              onChange(power);
            },
          }}
          secondaryAction={{
            label: "Cancel",
            onPress: () => setIsOpen(false),
          }}
        >
          <Stack space={8}>
            <SliderField
              type="single"
              name="power"
              value={power}
              onChange={(e: number) => {
                console.log("Power is updated for pump", e);
                setPower(e);
              }}
              label="Pump Power"
              minValue={10}
              maxValue={100}
              step={1}
            />
            <Banner
              kind="informative"
              title="Notice"
              description="Power require an external pump to be connected to the machine, other wise it is simply ON/OFF"
              action={{
                label: "Learn more",
                onPress: () => {
                  window.open("https://www.google.com");
                },
              }}
            />
          </Stack>
        </Modal>
      )}
    </>
  );
};
