import { EnumTarget } from "@/gateway/step";
import useDeviceContext from "@/hooks/useDeviceContext";
import { Banner, Modal, SliderField, Stack } from "@buildo/bento-design-system";
import { produce } from "immer";
import { useEffect, useRef, useState } from "react";

export function WeightRune({
  // value,
  //   onChange,
  // progress,
  subIndex = -1,
  index,
}: {
  // value: number;
  // onChange: Dispatch<SetStateAction<number>>;
  // progress: number;
  subIndex: number;
  index: number;
}) {
  const [isOpen, setIsOpen] = useState(false);
  const { formulas, setFormulas, selectedFormulaId, formulaFeedbacks } =
    useDeviceContext();
  const step = formulas[selectedFormulaId].steps[index];
  const [weight, setWeight] = useState(step.target.value);

  const [current, setCurrent] = useState<number>(0);

  /*
      Progress doesn't always show up, it will disappear after 5 seconds
      So after 5 seconds, it must be clear to null
      So the number has its own internal state
  */

  useEffect(() => {
    const updated =
      formulaFeedbacks[selectedFormulaId]?.[index]?.target?.value || 0;
    setCurrent(updated * 10);
  }, [formulaFeedbacks, index, selectedFormulaId]);

  let timeout = useRef(null);

  // useEffect(() => {
  //   setCurrent(progress);

  //   clearTimeout(timeout.current)
  //   timeout.current = setTimeout(() => {
  //     setCurrent(null)
  //   }, 5000)
  //   console.log("Progress is updated for weight", progress)
  // }, [progress])

  function handleChangeAction() {
    setFormulas(
      produce(formulas, (draft) => {
        draft[selectedFormulaId].steps[index].target.targetType =
          EnumTarget.Weight;
        draft[selectedFormulaId].steps[index].target.value = weight;
      }),
    );
  }

  return (
    <>
      {current != null && (
        <h1
          className=" text-xl font-bold text-green-800"
          onClick={() => setIsOpen(true)}
        >
          {`${(current / 10).toFixed(1)}/`}
        </h1>
      )}

      <h1
        className=" text-xl font-bold text-blue-800"
        onClick={() => setIsOpen(true)}
      >
        {step.target.value.toFixed(1)}
        {step.target.value <= 1 ? " gram" : " grams"}
      </h1>
      {isOpen && (
        <Modal
          size="small"
          title="How much ?"
          onClose={() => setIsOpen(false)}
          primaryAction={{
            label: "OK",
            onPress: () => {
              setIsOpen(false);
              handleChangeAction();
            },
          }}
          secondaryAction={{
            label: "Cancel",
            onPress: () => setIsOpen(false),
          }}
        >
          <Stack space={8}>
            <SliderField
              type="single"
              name="weight"
              value={weight}
              onChange={setWeight}
              label="Weight"
              minValue={0}
              maxValue={500}
              step={1}
            />
            <Banner
              kind="informative"
              title="Reminder"
              description="Calibration is required for accurate weight, please go to Settings"
            />
          </Stack>
        </Modal>
      )}
    </>
  );
}
